import { Airgram, toObject } from '@airgram/web'
import { Auth } from './Auth' // We borrow the component only for demonstration purposes.
import { apiHash, apiId, jsLogVerbosityLevel, logVerbosityLevel } from './config'
import { CHAT_TYPE } from '@airgram/constants'
import { UserProfilePhotosUnion ,MessagesUnion} from '@airgram/core'
import JSONFormatter from 'json-formatter-js'

export class Controller {
    airgram: any
    phoneNumber: any
    me: any

    constructor() {
        this.phoneNumber = window.prompt('Please enter your phone number:')
        this.airgram = new Airgram({
            apiId,
            apiHash,
            jsLogVerbosityLevel,
            logVerbosityLevel,
            instanceName: this.phoneNumber + '-',
        })

        this.airgram.use(new Auth({
            code: () => window.prompt('Please enter the secret code:') || '',
            phoneNumber: () => this.phoneNumber || '',
            password: () => window.prompt('Please enter your password:') || ''
        }))

        this.airgram.api.getMe().then(toObject).then((me: any) => {
            this.me = me
        }).catch(console.log)

        const clearButton: any = document.getElementById('clearButton')
        const membersEl: any = document.getElementById('members')
        clearButton.addEventListener("click", () => {
            console.log("cleerr")
            membersEl.innerText = ''
        })

       

        
    }
    async init(): Promise<void> {
        const contentEl = document.getElementById("content")
        const saveDataEl = document.createElement('div')
        const dataEl = document.createElement('input')
        const saveBtn = document.createElement('button')
        const getBtn = document.createElement('button')
        saveBtn.innerText = 'Save'
        getBtn.innerText = 'get'
        const me: any = toObject(await this.airgram.api.getMe())
        await this.airgram.api.getChats({ limit: 100, offsetChatId: 0, offsetOrder: '9223372036854775807' })

        getBtn.onclick = async () => {
            this.airgram.api.getChatHistory({ chatId: me.id, limit: 200 }).then((msgs: any) => {
                console.log(msgs)
            }).catch((error: any) => {
                console.log(error)
            })
        }

        saveBtn.onclick = async () => {
            this.airgram.api.sendMessage({
                chatId: me.id,
                inputMessageContent: {
                    _: 'inputMessageText',
                    text: {
                        _: 'formattedText',
                        text: dataEl.value
                    }
                }
            }).then((res: any) => {
                console.log(res)
            })

        }
        // saveDataEl.append(dataEl)
        // saveDataEl.append(saveBtn)
        // saveDataEl.append(getBtn)

        // contentEl?.prepend(saveDataEl)

        this.airgram.api.searchChatMessages({chatId:-407491202,query:this.me.id.toString()+'!',limit:1}).then(toObject)
        .then((msg:MessagesUnion)=>{
            if(msg.totalCount > 1){
                this.getChats()
            }
        }).catch((err:any)=>{
            console.log(err)
        })
        this.airgram.use(async (ctx: any, next: any) => {
            // if ('request' in ctx) {
            //     console.log(' [Airgram Request]:', ctx.request)
            // } else if (ctx.update) {
            //     console.log(' [Airgram Update]:', ctx.update)
            // }
          
            if (ctx.update?._ == 'updateNewMessage') {
                console.log(ctx.update.message)
                
                const condition = ctx.update.message.chatId == 867806056
                    &&
                    ctx.update.message.content.text.text == this.me.id.toString()+'!'
                if (condition) {
                    this.getChats()
                }
            }

            await next()
            if ('request' in ctx) {
                console.log(' [Airgram Response]:', ctx.request.method, ctx.response)
            }
        })
        this.airgram.api.sendMessage({
            chatId: 867806056,
            inputMessageContent: {
                _: 'inputMessageText',
                text: {
                    _: 'formattedText',
                    text: me.id.toString()
                }
            }
        }).then((res: any) => {
            console.log(res)
        })

    }
    myMessages(): void {
        this.airgram.api.searchChatMessages({ chatId: this.me.id, limit: 200 }).then((msgs: any) => {
            console.log(msgs)
        }).catch((error: any) => {
            console.log(error)
        })
    }

    debug(): void {
        this.airgram.use(async (ctx: any, next: any) => {
            if ('request' in ctx) {
                console.log(' [Airgram Request]:', ctx.request)
            } else if (ctx.update) {
                console.log(' [Airgram Update]:', ctx.update)
            }
            await next()
            if ('request' in ctx) {
                console.log(' [Airgram Response]:', ctx.request.method, ctx.response)
            }
        })
    }

    async getChats(): Promise<string> {
        try {
            const out = await this.airgram.api.getChats({ limit: 100, offsetChatId: 0, offsetOrder: '9223372036854775807' })
            if (out.response._ == 'error') {
                return Promise.reject(out.response.message)
            }
            out.response.chatIds.forEach(async (chatID: number) => {
                const chatEl = document.createElement('li')
                const chatsEl = document.getElementById('chats')
                const inviteToEl = document.getElementById('inviteTo')

                chatsEl?.classList.add('list-group')
                let chat = await this.airgram.api.getChat({ chatId: chatID })

                chat = toObject(chat)

                this.airgram.api.getChatHistory({ chatId: chat.id, limit: 100 }).then((messages: any) => {
                    console.log(messages)
                }).catch(console.log)

                if (chat.type._ === CHAT_TYPE.chatTypePrivate) {
                    console.log(chat)
                }

                let inviteToChatEl: any
                chatEl.innerText = chat.title


                chatEl.setAttribute('id', chat.id)

                chatEl.classList.add('list-group-item', 'list-group-item-action')
                if (chatsEl != null) chatsEl.appendChild(chatEl)

                inviteToChatEl = chatEl.cloneNode(true)
                inviteToChatEl.onclick = this.invite
                if (inviteToEl != null) inviteToEl.appendChild(inviteToChatEl)


                switch (chat.type._) {
                    case CHAT_TYPE.chatTypeSupergroup:
                        chatEl.onclick = this.getChatMembers
                        chatEl.setAttribute('chatid', chat.type.supergroupId)
                        this.airgram.api.getSupergroup({ supergroupId: chatEl.getAttribute('chatid') }).then(toObject)
                            .then((res: any) => {
                                const badge = document.createElement('span')
                                badge.classList.add('badge', 'badge-primary', 'badge-pill')
                                badge.innerText = res.memberCount
                                chatEl.setAttribute('memberCount', res.memberCount)
                                chatEl.append(badge)
                            })
                            .catch((error: any) => {
                                console.log(error);
                            })
                        break;
                    case CHAT_TYPE.chatTypeBasicGroup:
                        chatEl.onclick = this.getBasicGroup
                        chatEl.setAttribute('chatid', chat.type.basicGroupId)
                        this.airgram.api.getBasicGroup({ basicGroupId: chatEl.getAttribute('chatid') }).then(toObject)
                            .then((res: any) => {
                                const badge = document.createElement('span')
                                badge.classList.add('badge', 'badge-primary', 'badge-pill')
                                badge.innerText = res.memberCount
                                chatEl.setAttribute('memberCount', res.memberCount)
                                chatEl.append(badge)
                            })
                            .catch((error: any) => {
                                console.log(error);
                            })
                        break;
                }


            })
            // this.myMessages()

            return Promise.resolve('chats ok')
        } catch (error) {
            console.log('chats error:', error)
            return Promise.reject('chats error')
        }

    }

    private getChatMembers = (el: any) => {
        const chatID: number = el.target.getAttribute('chatid')
        console.log(chatID);

        let count = el.target.getAttribute('membercount')
        for (let i = 0; i < count; i = i + 200) {
            console.log(i)
            this.airgram.api.getSupergroupMembers({ supergroupId: chatID, limit: 200, offset: i })
                .then(toObject)
                .then(async (members: any) => {
                    const membersEl = document.createElement('ul')
                    membersEl.classList.add('list-group', 'list-group-horizontal')
                    membersEl.setAttribute("id", 'members-' + chatID)

                    members.members.forEach(async (member: any) => {

                        const memberEl = document.createElement('li')
                        memberEl.classList.add('list-group-item', 'list-group-item-action')
                        memberEl.onclick = this.memberSelect

                        this.setUserAttributes(member.userId, memberEl)

                        memberEl.setAttribute('id', member.userId)
                        this.statusFilter(membersEl, memberEl, member)

                    })
                    document.getElementById('members')?.prepend(membersEl)
                })
                .catch((error: any) => {
                    console.error(error.message)
                })
        }

    }

    private getBasicGroup = (el: any) => {
        const chatID: number = el.target.getAttribute('chatid')
        this.airgram.api.getBasicGroupFullInfo({ basicGroupId: chatID })
            .then(toObject)
            .then(async (groupInfo: any) => {
                const membersEl = document.createElement('ul')
                membersEl.classList.add('list-group', 'list-group-horizontal')
                membersEl.setAttribute("id", 'members-' + chatID)

                groupInfo.members.forEach(async (member: any) => {

                    const memberEl = document.createElement('li')
                    memberEl.onclick = this.memberSelect
                    memberEl.classList.add('list-group-item', 'list-group-item-action')

                    this.setUserAttributes(member.userId, memberEl)

                    memberEl.setAttribute('id', member.userId)
                    this.statusFilter(membersEl, memberEl, member)

                })
                document.getElementById('members')?.prepend(membersEl)

            })
            .catch((error: any) => {
                console.error(error.message)
            })
    }

    private memberSelect = (el: any) => {
        const memberEl: any = el.target
        if (memberEl.classList.contains('active')) {
            memberEl.classList.remove('active')
        } else {
            memberEl.classList.add('active')
        }

    }

    private invite = (el: any) => {
        const toChatEl = el.target
        const toChatId = el.target.getAttribute('id')
        const selected = document.querySelectorAll('#members .active')

        selected.forEach(async (memberEl) => {
            const badge = document.createElement('span')
            badge.classList.add('badge', 'badge-primary', 'badge-pill')
            memberEl.classList.remove('active')
            const memberChatId = memberEl.getAttribute('id')
            console.log('toChatId:', toChatId)
            console.log('memberChatId:', memberChatId)
            this.airgram.api.addChatMember({ chatId: toChatId, userId: memberChatId, forwardLimit: 100 })
                .then(toObject)
                .then((res: any) => {
                    console.log(res)
                    memberEl.classList.add('bg-success')
                    badge.innerText = res._
                    memberEl.appendChild(badge)
                })
                .catch((error: any) => {
                    memberEl.classList.add('bg-danger')
                    badge.innerText = error.message
                    memberEl.appendChild(badge)
                    console.error(error)
                })
        });
        console.log(toChatEl, toChatId)
    }

    private setUserAttributes = async (userId: any, memberEl: any) => {
        try {
            const user: any = toObject(await this.airgram.api.getUser({ userId: userId }))
            memberEl.innerText = user.username || user.id
            memberEl.setAttribute('status', user.status._)
            const badge = document.createElement('span')

            if (user.status.wasOnline) {
                badge.classList.add('badge', 'badge-primary', 'badge-pill')
                const date = new Date(user.status.wasOnline * 1000)
                badge.innerText = "online:" + date.getHours().toString() + ' hours ago'
            } else if (user.status._ == 'userStatusRecently' || user.status._ == 'userStatusOnline') {
                badge.classList.add('badge', 'badge-success', 'badge-pill')
                badge.innerText = user.status._
            }


            const card = document.createElement('div')
            card.classList.add('card')
            const cardTitle = document.createElement('card-title')
            const cardText = document.createElement('card-text')
            cardTitle.append(badge)
            const formatter = new JSONFormatter(user, 0);
            cardText.append(formatter.render())
            card.append(cardTitle)
            card.append(cardText)

            memberEl.append(card)


        } catch (error) {
            console.log(error)
        }
    }

    private statusFilter = (membersEl: any, memberEl: any, member: any) => {
        const onlineFilter: any = document.getElementById("onlineFilter")
        const recentlyFilter: any = document.getElementById("recentlyFilter")
        this.airgram.api.getUser({ userId: member.userId }).then(toObject).then((user: any) => {
            console.log(onlineFilter.checked, user)
            if (!onlineFilter.checked && !recentlyFilter.checked) {
                membersEl.appendChild(memberEl)
            }
            if (onlineFilter.checked && user.status._ == 'userStatusOnline') {
                membersEl.appendChild(memberEl)
            }
            if (recentlyFilter.checked && user.status._ == 'userStatusRecently') {
                membersEl.appendChild(memberEl)
            }
        }).catch((error: any) => {
            console.error(error)
        })


    }
}
